.app{
  height: 100vh;
display: flex;
justify-content: center;
background-color: #282c34;
color: white;
}

.app-things{
  display: flex;
  align-items: center;
  /* justify-content: center; */
  flex-direction: column;
  border: 2px solid white;
  height: 80vh;
  font-size: 20px;
}
.todoform{
  display: flex;
  justify-content: space-around;
  width: 450px;
}
input{
  width:80%;
padding: 10px;
border-radius: 20px;
border: none;
}
button{
  border-radius: 20px;
  padding: 5px 20px;
  margin: 0 5px;
}

.Alltodo{
list-style: none;
margin: 0;
padding: 0;
}
.single_todo{
  border-radius: 50px;
  margin: 10px 0px;
  background-color: skyblue;
  padding: 10px 20px;
  display: flex;
  width: 450px;
  align-items: center;
}

.todo_text{
flex: 1;
}